.fundo{
    background-color: rgb(237, 237, 151);
    height: 95vh;
}


.imagemFundoSobre{
    background-image: url('/public/assets/banner-sobre.png');
    width: 100%;
    height: 100%;
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center center;;
}

.imagemFundoSobre h1{
    margin: 0;
    padding: 0;
    color: white;
    font-size: 80px;
    text-shadow: 5px 10px 10px black;  
    text-transform: uppercase;;
}

.sabores{
    padding: 80px 0;
}

.sabores h2{
    text-align:center;
    font-size: 40px;
    text-transform: uppercase;
    margin-bottom: 40px;
}

.secao_sabores{
    display:flex;
    width: 100%;
    flex-wrap: wrap;
}

.sabores_tipo{
    border:1px solid transparent;
    border-radius: 5px;
    box-shadow: 2px 2px 10px 0.5px rgb(117, 115, 115);;
    width: calc((100%/3) - 30px);
    margin: 15px;
    box-sizing: border-box;
    text-align: center;
    display: flex;
    flex-direction: column;
    justify-content: end;
}

.sabores_tipo img{
    width: 100%;
}

.sabores_tipo p{
    padding: 10px 15px;
    margin: 0 0 50px 0;   
    font-size: 18px;
}

.sabores_tipo h3{
    font-weight: 700;
    font-size: 24px;
}

@media screen and (max-width: 425px){
    .divTexto h1{
        font-size: 60px;
    }
    .sabores h2{
        font-size: 30px;
    }
    .secao_sabores{
        display: block;
        padding: 0 15px;
    }
    .sabores_tipo{
        width: 100%;
        display: block;
        margin: 20px 0;
    }
}

@media screen and (min-width: 426px) and (max-width:786px){
    .divTexto h1{
        font-size: 60px;
    }
    .sabores h2{
        font-size: 30px;
    }
    .secao_sabores{
        display: block;
        padding: 0 15px;
    }
    .sabores_tipo{
        width: 100%;
        display: block;
        margin: 20px 0;
    }
    .sabores_tipo img{
        width: 85%;
    }
}