.fundo{
    background-color: rgb(237, 237, 151);
    height: 95vh;
}


.imagemFundoSabores{
    background-image: url('/public/assets/banner-sabores.jpg');
    width: 100%;
    height: 100%;
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center center;;
}

.imagemFundoSabores h1{
    margin: 0;
    padding: 0;
    color: white;
    font-size: 80px;
    text-shadow: 5px 10px 10px black;  
    text-transform: uppercase;;
}

.secaoSobre{
    height: 35vh;
    text-align: center;
    margin-top: 60px;
    margin-bottom: 60px;
}

.secaoSobre h2{
    font-size: 40px;
    margin-bottom: 10px;
}

.secaoSobre span{
    font-weight: bold;
    font-size: 24px;
    margin-bottom: 50px;
}

.secaoSobre p{
    line-height: 24px;
    padding: 0 30px;
    margin-top: 30px;
    font-size: 20px;
}


.secao_imgaens{
    width: 100%;
    display: flex;
    height: 55vh;
    padding-top: 40px;
}

.secao_imagens .img1, .img2{
    width: calc((100% / 2) -10px);
    margin: 0;
}

.secao_imgaens .img1{
    background-image: url('/public/assets/sobre-image.jpg');
    background-size: cover;
    width: 100%;
    background-repeat: no-repeat;
}

.secao_imgaens .img2{
    background-image: url('/public/assets/sorveteria.jpg');
    background-size: cover;
    width: 100%;
    background-repeat: no-repeat;
}

@media screen and (max-width: 425px){
    .fundo h1{
        font-size: 60px;
    }
    .secaoSobre{
        text-align: center;
        height: 80vh;
        margin-bottom: 200px;
        margin-top: 80px;
    }
    .secaoSobre p{
        font-size: 24px;
    }
    .secao_imgaens{
        display: flex;
        flex-wrap: wrap;
        height: 80vh;
    }
    .secao_imagens .img1, .secao_imagens .img2{
        width: 50%;
    }
}

@media screen and (min-width: 426px) and (max-width:768px){
    .fundo h1{
        font-size: 60px;
    }
    .secaoSobre{
        text-align: center;
        height: 80vh;
        margin-bottom: 100px;
        margin-top: 80px;
    }
    .secaoSobre p{
        font-size: 24px;
    }
    .secao_imgaens{
        display: flex;
        flex-wrap: wrap;
        height: 80vh;
    }
    .secao_imgaens .img1 img, .secao_imgaens .img2 img{
        max-width: 500px;
    }
}