.fundo{
    background-color: rgb(237, 237, 151);
    height: 95vh;
}

.imagemFundo{
    background-image: url('/public/assets/banner-home.png');
    width: 100%;
    height: 95vh;
    background-size: contain;
    background-repeat: no-repeat;
    background-position: center center;;
}

.imagemFundo h1{
    margin: 0;
    padding: 0;
    color: white;
    font-size: 80px;
    text-shadow: 5px 10px 10px black;   
    text-transform: uppercase;
}

.divTexto{
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
}

.secao_conteudo{
    display: flex;
    align-items: center;
    justify-content: space-evenly;
    width: 100%;
}

.secao_conteudo img{
    width: 50%;
    height: 480px;
}

.secao_sorvete_conteudo{
    width: 50%;
    display: flex;
    align-items: center;
    flex-direction: column;
    justify-content: space-evenly;
    
}

.secao_conteudo h2{
    font-size: 30px;
    margin: 0;
    text-transform: uppercase;
}

.secao_conteudo h3{
    font-size: 16px;
    margin-top: 0;
    margin-bottom: 30px;
}

.secao_conteudo p{
    padding: 0 250px;
}

@media screen and (max-width: 425px) {
    .divTexto{
        text-align: center;
        
    }
    .divTexto h1{
        font-size: 60px;
    }
    .secao_conteudo{
        display: block;
        width: 100%;
    }
    .secao_conteudo p{
        padding: 0 60px;
        margin-bottom: 40px;
    }
    .secao_conteudo img{
        width: 100%;
    }
    .secao_sorvete_conteudo{
        width: 100%;
        margin: 40px 0;
        padding: 0px;
    }
    .evento{
        display: flex;
        flex-direction: column-reverse;
    }
}


@media screen and (min-width: 426px) and (max-width: 768px) {
    .divTexto{
        text-align: center;
    }
    .divTexto h1{
        font-size: 60px;
    }
    .secao_conteudo{
        display: block;
        width: 100%;
    }
    .secao_conteudo p{
        padding: 0 60px;
        margin-bottom: 40px;
    }
    .secao_conteudo img{
        width: 100%;
    }
    .secao_sorvete_conteudo{
        width: 100%;
        margin: 40px 0;
        padding: 0px;
    }
    .evento{
        display: flex;
        flex-direction: column-reverse;
    }
}

@media screen and (min-width: 769px) and (max-width: 1024px) {
    .secao_conteudo p{
        padding: 0 60px;
        margin-bottom: 40px;
    }
    
}