footer{
    background-color: black;
    color: white;
    width: 100%;
}

.rodape{
    display: flex;
    justify-content: center;
    padding-top: 40px;
}

.rodape p{
    font-size: 16px;
    margin: 5px;
}

.rodape h4{
    text-transform: uppercase;
}

.rodape img{
    width: 130px;
}

.caixaInfo{
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    padding: 0 30px;
    max-width: 200px;
}

.rodapeTexto{
    text-align: center;
    padding: 10px;
    margin-top: 20px;
    font-weight: bold;
    font-size: 20px;
}

@media screen and (max-width:425px){
    .rodape{
        display: flex;
        flex-direction: column;
        text-align: center;
        align-items: center;
        justify-content: space-evenly;
    }
    .caixaInfo{
        padding: 15px 0;
    }
    .caixaInfo h4{
        margin-bottom: 10px;
    }
    .rodapeTexto{
        font-size: 15px;
    }
}

@media screen and (min-width:426px) and (max-width:768px){
    .rodape{
        display: flex;
        flex-direction: column;
        text-align: center;
        align-items: center;
        justify-content: space-evenly;
    }
    .caixaInfo{
        padding: 15px 0;
    }
    .caixaInfo h4{
        margin-bottom: 10px;
    }
    .rodapeTexto{
        font-size: 18px;
    }
}

