.head{
    background-color: rgb(237, 237, 151);
}

.cabecalho{
    display: flex;
    justify-content: space-evenly;
    align-items: center;
}

.cabecalho .link{
    padding: 20px 15px;
    text-decoration: none;
    color: black;
    font-weight: 700;
    font-size: 20px;
}

.cabecalho img{
    width: 60px;
}

.link:hover{
    font-size: 22px;
    color: rgb(60, 37, 37);
}

@media screen and (max-width: 425px) {
    .cabecalho {
        display: flex;
        flex-direction: column;
    }
    .secaoNav{
        border-top: 1px solid black;
        padding-top: 5px;
    }
}

@media screen and (min-width: 426px) and (max-width: 768px) {
    .cabecalho {
        display: flex;
        flex-direction: column;
    }
    .secaoNav{
        border-top: 1px solid black;
        padding-top: 5px;
    }
}